.app {
    height: 100vh;

    .sections {
        width: 100%;
        height: calc(100vh - 70px);
        position: relative;
        top: 70px;
        scroll-snap-type: y mandatory;
        scroll-behavior: smooth;
        background-color: rgb(228, 162, 150);
        // removes scroll bar
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
        
        > *{
            width:100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}
